export const createWorkplace = async (workplace: any) => {
  // Handle null values for assigned IDs
  const workplaceData = {
    ...workplace,
    assignedExpertId: workplace.assignedExpertId || null,
    assignedDoctorId: workplace.assignedDoctorId || null,
    assignedDspId: workplace.assignedDspId || null,
    trackingExpertId: workplace.trackingExpertId || null
  };

  // Sanitize inputs
  const sanitizedData = sanitizeObject(workplaceData);

  const response = await fetch(`${API_BASE_URL}/workplaces`, {
    method: 'POST',
    headers: getHeaders(),
    body: JSON.stringify(sanitizedData),
  });
  
  if (!response.ok) {
    const errorData = await response.json().catch(() => ({}));
    const errorMessage = errorData.message || `Failed to create workplace: ${response.status} ${response.statusText}`;
    throw new Error(errorMessage);
  }
  
  const data = await response.json();
  console.log('createWorkplace response:', data);
  
  // Clear cache for workplaces
  clearCache('workplaces');
  
  return data;
};

export const updateWorkplace = async (id: number, workplace: any) => {
  // Handle null values for assigned IDs
  const workplaceData: Record<string, any> = {
    ...workplace,
    assignedExpertId: workplace.assignedExpertId || null,
    assignedDoctorId: workplace.assignedDoctorId || null,
    assignedDspId: workplace.assignedDspId || null,
    trackingExpertId: workplace.trackingExpertId || null
  };

  // Remove any undefined values
  Object.keys(workplaceData).forEach(key => 
    workplaceData[key] === undefined && delete workplaceData[key]
  );

  // Sanitize inputs
  const sanitizedData = sanitizeObject(workplaceData);

  const response = await fetch(`${API_BASE_URL}/workplaces/${id}`, {
    method: 'PUT',
    headers: getHeaders(),
    body: JSON.stringify(sanitizedData),
  });
  
  if (!response.ok) {
    const errorData = await response.json().catch(() => ({}));
    const errorMessage = errorData.message || `Failed to update workplace: ${response.status} ${response.statusText}`;
    throw new Error(errorMessage);
  }
  
  const data = await response.json();
  
  // Clear cache for workplaces
  clearCache('workplaces');
  
  return data;
};

