import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import './Navbar.css';

const Navbar = () => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  return (
    <nav className="navbar">
      <div className="navbar-brand">
        <Link to="/">OSGB System</Link>
      </div>
      
      <ul className="navbar-nav">
        <li className="nav-item">
          <Link to="/" className="nav-link">Dashboard</Link>
        </li>
        <li className="nav-item">
          <Link to="/experts" className="nav-link">Experts</Link>
        </li>
        <li className="nav-item">
          <Link to="/doctors" className="nav-link">Doctors</Link>
        </li>
        <li className="nav-item">
          <Link to="/dsps" className="nav-link">DSPs</Link>
        </li>
        <li className="nav-item">
          <Link to="/workplaces" className="nav-link">Workplaces</Link>
        </li>
        <li className="nav-item">
          <Link to="/visits" className="nav-link">Visits</Link>
        </li>
      </ul>
      
      <div className="navbar-user">
        {user && (
          <>
            <span className="user-name">{user.fullName}</span>
            <button className="logout-button" onClick={handleLogout}>
              Logout
            </button>
          </>
        )}
      </div>
    </nav>
  );
};

export default Navbar;